<script>
import { h } from 'vue'
export default {
  name: 'XtxBread',
  render () {
    // 用法
    // 1.template 标签去除,单文件组件
    // 2.返回值就是组件内容
    // 3.vue2.0 的h函数是传参进来的,vue3.0的h函数是需要导入进来的
    // 4.h 参数1 标签名 参数2 标签属性对象 参数3 子节点
    // 需求
    // 1.创一个带xtx-bread父容器
    // 2.获取默认插槽内容
    // 3.去除xtx-breaad-item组件内的i标签，这个应该由render生成
    // 4.遍历插槽中的item，得到一个动态创建的节点，最后一个item不加i标签
    // 5.把动态创建的节点渲染在xtx-bread标签中
    const items = this.$slots.default()
    const dymanicItems = []
    items.forEach((item, i) => {
      dymanicItems.push(item)
      if (i < items.length - 1) {
        dymanicItems.push(h('i', { class: 'iconfont icon-angle-right' }))
      }
    })
    return h('div', { class: 'xtx-bread' }, dymanicItems)
  }
  // props: {
  //   // 父级类目名称
  //   parentName: {
  //     type: [String, Object],
  //     default: ''
  //   },
  //   // 父级类目路径
  //   parentPath: {
  //     type: String,
  //     default: ''
  //   }
  // }
}
</script>

<style lang="less">
.xtx-bread {
  display: flex;
  padding: 25px 10px;
  &-item {
    a {
      color: #666;
      transition: all 0.4s;
      &:hover {
        color: @xtxColor;
      }
    }
    // &:last-of-type {
    //   i {
    //     display: none;
    //   }
    // }
  }
  i {
    font-size: 12px;
    margin-left: 5px;
    margin-right: 5px;
    line-height: 22px;
  }
}
</style>
