<template>
  <div class="topBtn" @click.prevent="topBtn">↑</div>
</template>

<script>
export default {
  name: 'XtxTopBtn',
  setup () {
    const scrollAnimation = (currentY, targetY) => {
      // 获取当前位置方法
      // 计算需要移动的距离
      let needScrollTop = targetY - currentY
      let _currentY = currentY
      setTimeout(() => {
        // 一次调用滑动帧数，每次调用会不一样
        const dist = Math.ceil(needScrollTop / 60)
        _currentY += dist
        window.scrollTo(_currentY, currentY)
        // 如果移动幅度小于十个像素，直接移动，否则递归调用，实现动画效果
        if (needScrollTop > 60 || needScrollTop < -60) {
          scrollAnimation(_currentY, targetY)
        } else {
          window.scrollTo(_currentY, targetY)
        }
      }, 1)
    }
    const topBtn = () => {
      const currentY =
        document.documentElement.scrollTop || document.body.scrollTop
      scrollAnimation(currentY, 0)
    }

    return { topBtn }
  }
}
</script>

<style lang="less" scoped>
.topBtn {
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50%;
  background-color: #33333381;
  color: #ffffff;
  position: fixed;
  top: 50%;
  right: 50px;
  font-size: 20px;
  text-align: center;
  z-index: 1;
  margin-top: -25px;
  transition: all 1.5s;
  cursor: pointer;
  &:hover {
    transition: all 1.5s;
    background-color: @xtxColor;
  }
}
</style>
