// 1.初始化axios实例
// 2.请求拦截器,带token
// 3.响应拦截器,1.剥离无效数据 2.处理token失效
// 4.导出一个函数,调用当前的axios实例发请求,返回值promise
import axios from 'axios'
import store from '@/store'
import router from '@/router'
// 导出基准地址,原因:其他地方有可能不是通过axios发请求,但也要用上基准地址.
export const baseURL = 'http://pcapi-xiaotuxian-front-devtest.itheima.net/'
const instance = axios.create({
  // axios的一些配置
  baseURL: baseURL,
  timeout: 5000
})

// 添加请求拦截器
instance.interceptors.request.use(
  config => {
    // 拦截业务逻辑
    // 进行请求配置的修改
    // 如果本地有token就在头部携带
    // 1.获取用户信息对象从里面拿token
    const { profile } = store.state.user
    // 2.判断是否有token
    if (profile.token) {
      // 3.设置token到请求头
      // 需要授权的 API ，必须在请求头中使用 Authorization 字段提供 token 令牌
      config.headers.Authorization = `Bearer ${profile.token}`
    }
    return config
  },
  error => {
    // 对请求错误做些什么
    return Promise.reject(error)
  }
)

// 添加响应拦截器
instance.interceptors.response.use(
  // response拿到的是后台接口响应成功后的数据 我们只返回我们需要的数据,剥离无用的数据
  response => {
    // 对响应数据做点什么
    return response.data
  },
  error => {
    // 对响应错误做点什么
    // 401 状态码,进入该函数
    if (error.response && error.response.status === 401) {
      // 1.清空无效用户信息
      store.commit('user/setUser', {})
      // 2.跳转到登录页面
      const fullPath = encodeURIComponent(router.currentRoute.value.fullPath)
      router.push('/login?redirectUrl=' + fullPath)
      // 3.跳转需要传参(当前路由地址)需要给登录页面 当登录了还要跳转回之前的页面
    }
    return Promise.reject(error)
  }
)

// 请求工具函数
const request = (url, method, submitData) => {
  // 负责发请求:请求地址,请求方式,提供数据
  return instance({
    url,
    method,
    // 1.如果是get请求,需要用parmas来传递
    // 2.如果是post请求,需要用data来传递
    [method.toLowerCase() === 'get' ? 'params' : 'data']: submitData
  })
}

export default request
